.container-fluid {
  width: 100% !important;
  margin: 0 auto !important;
}

.card {
    background-size: cover;
    background-position: center;
    background-clip: content-box;
    border: 6px solid white;
    height: 400px;
    width: 400px;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    position: relative;
    margin: 0 auto;
    margin-top: 30px;
  }
  
  .card .fa-spinner {
    font-size: 180px;
    position: absolute;
    left: 25%;
    top: 25%;
  }
  
.card-img-top {
    height: 250px;
    width: 350px;
}

.card-title {
  text-align: center;
}

